.navbar {
  background-color: #fe0000;
  box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.75);
}

.buscar-button {
  background-color: white !important;
}

.navbar-brand,
.nav-link {
  font-family: Title;
  text-shadow: -5px 4px 9px rgba(0, 0, 0, 0.81);
}

.nombre-navbar {
  font-family: Title;
  text-shadow: -5px 4px 9px rgba(0, 0, 0, 0.81);
}

.buscador {
  width: 60vh;
}

.logo-navbar {
  width: 9vh;
}

.logo-navbar-hover:hover {
  transition: all 0.2s;
  transform: scale(1.05);
}

.nav-link:hover {
  transition: all 0.2s;
  transform: scale(1.1);
}

@media (min-width: 768px) and (max-width: 991.98px) {
  .navbar-brand {
    display: none;
  }
  .buscador {
    width: 30vh;
  }
  .logo-navbar {
    width: 7vh;
  }
}

@media (max-width: 767.98px) {
  .navbar-brand {
    display: none;
  }
  .buscador {
    width: 19vh;
  }
  .logo-navbar {
    width: 7vh;
  }
  .nav-link {
    padding-top: 15px;
    font-size: 2.5vh !important;
  }
}
