.links-admin-categoria {
  background-color: #fe0000;
}

.link-admin-categoria {
  width: 30%;
  font-size: 3vh;
  cursor: pointer;
}

.link-admin-categoria:active {
  color: #abcc01 !important;
}

.link-admin-categoria:focus {
  background-color: #abcc01 !important;
}

.link-admin-categoria:hover {
  transition: all 0.2s;
  transform: scale(1.05);
}

@media (min-width: 768px) and (max-width: 991.98px) {
  .link-admin-categoria {
    width: 40%;
    font-size: 3vh;
    text-align: center;
  }
}

@media (max-width: 767.98px) {
  .link-admin-categoria {
    width: 40%;
    font-size: 2.5vh;
    text-align: center;
  }
}
